
@import "../../../style/Base/all";
.Home_3{
    // background-color:black;
    @include fp{
        background-color: map-get($color, bg );
        @include section(s48, padding-bottom);
        @include section(s48, padding-top);
    }
    height: 100%;
    .container{
        height: 100%;

    }
    &-wrapper{
        height: 100%;
        width: 100%;
    }
}
#dp-edu-cer{
    height: 100%;
    width: 100%;
    .edu-cer{
        display: flex;
        align-items: center;
        @include fp{
           flex-direction: column;
        }
        width: 100%;
        height: 100%;
        &_left{
            position: relative;
            width: 40%;
            height: 100%;
            @include fp{
                width: 100%;
                margin-bottom: 100px;
            }
            display: flex;
            justify-content: center;
            flex-direction: column;
            z-index: 3;
        }
        &_title{
            color: map-get($color, text1);
            @include fp{
                color: white;
            }
        }
        &_swiper{
            width: 100%;
            @include section(s70, margin-top);
            position: relative;
            .myEduCerSwiper{

                padding: 10px 0;
                padding-right: 10px;
                .swiper-slide{
                    height: unset !important;
                }
            }
        }
        &_right{
            height: 100%;
            width: 50%;
            @include fp{
                width: 100%;
                
            }
            margin-left: auto;
            display: flex;
            justify-content: center;
            flex-direction: column;
            &-wrapper{
                max-height: 400px;
                width: 100%;
                @include rp(tx){
                    max-height: 300px;
                }
                overflow-y: scroll;
                &::-webkit-scrollbar {
                    width: 10px;
                   @include rp(mx){
                    width: 6px;
                   }
                }
                /* Track */
                &::-webkit-scrollbar-track {
                    background: #f1f1f1;
                }
                
                /* Handle */
                &::-webkit-scrollbar-thumb {
                background: map-get($color, sub1 );;
                }
                
                /* Handle on hover */
                &::-webkit-scrollbar-thumb:hover {
                    background: #555;
                }
            }
        }
    }
    
}