@import '../../Base/all';

.Home_1{
  position: relative;
  height: 100%;
  .arrow-down{
    position: absolute;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
    &::after{
      position: absolute;
      bottom: 50px;
      left: 50%;
      transform: translateX(-50%);
      height: 40px;
      width: 5px;
      border-radius: 10px;
      content: "";
      background-color: map-get($color, sub1 );
    }
    @include fp{
      opacity: 0 !important;
    }
  }
}
#banner{
  height: 100%;
}
.banner{
    height: 100%;
    width: 100%;
    position: relative;
    overflow: hidden;
    transition: .3s ease-in-out;
    // background-color: map-get($color, text2 );
    
    &.bannerSub{
      .banner-section__decs{
        
        &--infor{
          &_title{
            @include title(s84);
          }
          &_decs{
            @include title(s54);
          }
        }
       
      }
      
    }
    .banner-section{
      position: absolute;
      top: 50%;
      left: 50%;
      height: 100%;
      transform: translate(-50%,-50%);
      // overflow: hidden;
      @include rp(tl){
        position: unset;
        transform:unset;
        height: 100%;
      }
      
      .wrapper{
        height: 100%;
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        position: relative;
        // margin: 0 -8px;
        // justify-content: space-between;
        // &>*{
        //   flex: 1 1;
        // }
        align-items: center;
        @include rp(tl){
          justify-content: center;
        }
      }
      &__img{
        padding: 0 8px;
        animation-fill-mode: both;
        position: absolute;
        bottom: 0;
        right: 0;
        height: 100%;
        @include rp(ts){
          opacity: 0.5 !important;
        }
        
        .img{
          position: relative;
          z-index: 2;
          width: 100%;
          background: transparent;
          overflow: hidden;
          height: 100%;
          @include rp(ts){
            z-index: 1;
          }
          img{
            width: 100%;
            height: 100%;
            object-fit: contain;
            z-index: 1;
           

          }
        
        }
       
      }
      &__decs{
        margin-right: auto;
        padding: 0 8px;
        position: relative;
        @include rp(ts){
          z-index: 2;
        }
        &--infor{
          display: flex;
          flex-direction: column;
          &_title{
            @include title(s101);
            @include p-line-clamp(3);
            margin-bottom: 20px;
            color: map-get($color, text1 );
          }
          &_decs{
            @include title(s64);
            @include p-line-clamp(8);
            color: map-get($color, text3 );
          }
          &_time{
            @include section(s48,margin-top);
            color: map-get($color, sub1 );
            @include title(s84);
            margin-left: auto;
          }
        }
      }
    }
  }