@import '../../style/Base/all';


.information__wrapper--list_items{
    padding: 8px;
    
    .items{
        background-color: white;
        height: 100%;
        padding: 40px;
        transition: .3s ease-in-out;
        cursor: pointer;
        min-height: 400px;
        display: flex;
        flex-direction: column;
        @include rp(pl){
            min-height: 320px;
            padding: 25px;
        }
        @include rp(tx){
            min-height: 250px;
        }
        @include rp(ts){
            min-height: 200px;
        }
        &>*{
            flex: 1 2;
            transition: .2s ease-in-out;
            color: map-get($color, text1 );
        }
        &-name{
            position: relative;
            z-index: 2;
            @include title(s54);
        }
        &-decs{   
            word-wrap: break-word;  
            @include title(s36);
        }
        &:hover{
            background-color: map-get($color, sub1 );
            
        }
    }
    
}