@import '../Base/all';

#header{
    width: 100%;
    position: fixed;
    z-index: 999;
    transition: .3s ease-in-out;
    // backdrop-filter: grayscale(0.5) opacity(0.8);
    // backdrop-filter: blur(3px);
    background-color: transparent;
    @include fp{
        position: unset;
    }
    .header-wrapper{
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        padding: 0 20px;
        &__logo{
            max-width: 100px;
            transition: .3s ease-in-out;
            position: relative;
            .nav_link{
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                z-index: 1;
            }
            @include rp(tm){
                max-width: 80px;
            }
            svg{
                padding: 10px;
                width: 100%;
                transition:transform .3s ease-out;
                path{
                    transition: .3s ease-in-out;
                }
                circle{
                    transition: .3s ease-in-out;
                }
                #Logo_1{
                    fill: transparent;
                    stroke: 10;
                    stroke: #3a3a3a;
                    stroke-dasharray: 250;
                    stroke-dashoffset: 250;
                    animation: animate_1 1 1.5s linear forwards;
                    @keyframes animate_1 {
                        0%{
                            stroke-dashoffset: 0;
                        }
                        40%{
                            stroke-dashoffset: 250;
                        }
                        70%{
                            stroke-dashoffset: 500;
                            fill: transparent;
                        }
                        100%{
                            stroke-dashoffset: 500;
                            fill: #3a3a3a;
                        }

    
                    }
                }
                #Logo_2{
                    // fill: #3a3a3a;
                    stroke: 10;
                    // stroke: #3a3a3a;
                    path{
                        fill: transparent;
                        stroke: 10;
                        stroke: #3a3a3a;
                        stroke-dasharray: 250;
                        stroke-dashoffset: 250;
                        animation: scale_2 1 1.5s ease-in-out forwards;
                        animation-delay: .3s;
                        @keyframes scale_2 {
                            0%{
                                stroke-dashoffset: 0;
                            }
                            40%{
                                stroke-dashoffset: 250;
                            }
                            70%{
                                stroke-dashoffset: 500;
                                fill: transparent;
                            }
                            100%{
                                stroke-dashoffset: 500;
                                fill: #3a3a3a;
                            }
    
        
                        }
                    }

                    
                }
            }
            &:hover{
                svg{
                    transform:scale(.9);
                    transition:transform .2s cubic-bezier(.3,.86,.61,1.63);
                }
                
            }
        }
        &__nav{
            min-width: 80px;
            height: auto;
            margin-left: auto;
            display: flex;
            align-items: center;
            position: relative;
            .nav_link{
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                z-index: 1;
            }
            &-title{
                
                @include title(s20);
                white-space: nowrap;
                margin-right: 20px;
                display: flex;
                align-items: center;
                font-family: 'LINOTTE-BOLD', sans-serif;
                color: map-get($color, text3 );
                transform: translateX(50px);
                opacity: 0;
                transition: opacity .3s ease-out;
                svg{
                    margin-left: 10px;
                    width: 15px;
                    height: 15px;
                    transform: rotate(90deg);
                }
                @include rp(ts){
                    display: none;
                }
            }
            &-hamburger{
                width: 100%;
                height: 100%;
                display: flex;
                flex-direction: column;
                align-items: flex-end;
                cursor: pointer;
                overflow: hidden;
                &>*:not(:last-child){
                    margin-bottom: 5px;
                }
                .line-1{
                    width: 80px;
                    height: 9px;
                    border-radius: 20px;
                    background-color: map-get($color, bg );
                    // transition: .2s ease-in-out;
                   
                }
                .line-2{
                    width: 60px;
                    height: 9px;
                    border-radius: 20px;
                    background-color: map-get($color, bg );
                    transition: .2s ease-in-out;
                }
                .line-3{
                    width: 40px;
                    height: 9px;
                    border-radius: 20px;
                    background-color: map-get($color, bg );
                    transition: .2s ease-in-out;
                }
                @include rp(ts){
                    .line-1{
                        width: 55px;
                        height: 8px;
                    }
                    .line-2{
                        width: 35px;
                        height: 8px;
                    }
                    .line-3{
                        width: 15px;
                        height: 8px;
                    }
                }
            }
            &:hover{
                .line-1{
                    animation: flowLeft .6s 1 ease-in-out;
                }
                .line-2{
                    animation: flowLeft .8s 1 ease-in-out;
                }
                .line-3{
                    animation: flowLeft 1s 1 ease-in-out;
                }
                @keyframes flowLeft {
                    0%{
                        transform: translateX(0px);
                        opacity: 1;
                    }
                    10%{
                        transform: translateX(10px);
                        // opacity: 0;
                    }
                    40%{
                        transform: translateX(-50px);
                        opacity: 0;
                    }
                    60%{
                        transform: translateX(100px);
                        opacity: 0;
                    }
                    90%{
                        transform: translateX(-5px);
                        // opacity: 0;
                    }
                    100%{
                        transform: translateX(0px);
                        opacity: 1;
                    }
                }
                // .header-wrapper__nav-title{
                //     transform: translateX(0);
                //     opacity: 1;
                    // animation: flowRight .3s 1 ease-in-out reverse;
                    @keyframes flowRight {
                        0%{
                            transform: translateX(50px);
                            opacity: 0;
                        }
                        60%{
                            transform: translateX(-10px);
                            opacity: 1;
                        }
                        100%{
                            transform: translateX(0px);
                            opacity: 1;
                        }
                    }
                    @keyframes flowRight_Reverse {
                        0%{
                            transform: translateX(0px);
                            opacity: 1;
                            
                        }
                        60%{
                            transform: translateX(-10px);
                            opacity: 1;
                        }
                        100%{
                            transform: translateX(50px);
                            opacity: 0;
                        }
                    }
                // }
            }
        }
    }
}