
@import '../Base/all';
.section{
    overflow: hidden;
}

.mr-r-fp{
	margin-right: 3%;
}

#fullpage{
    .section{
        overflow: hidden;
        @include fp{
            // overflow: auto;
            height: auto !important;
        
            .fp-tableCell{
                height: auto !important;
            }
        }
    }
    @include fp{
        height: auto !important;
        position: initial !important;
        transform: none !important;
    }
}
#fp-nav{
    right: unset;
    left: 10px;
    ul{
        li{
            a,a.active{
                span{
                    border-radius: 0 !important;
                    width: 12px !important;
                    height: 12px !important;
                    margin: unset !important;
                    // transform: translate(-50%, -50%) !important; 
                    
                }
            }
            a{
                span{

                    background: map-get($color, sub1 )!important;
                }
            }
            a.active,a:hover{
                span{
                    width: 30px !important;
                }
            }
        }
    }
}