@import '../Base/all';
.bg-banner{
    position: fixed;
    top: 0;
    right: 0;
    height: 100%;
    // width: 50%;
    transform: scaleX(1.1);
    transition: 1s ease-in-out;
    svg{
        width: 100%;
        height: 100vh;
        transition: 1s ease-in-out;
    }
    // height: 100%;
    // width: 50%;
    &.active{
        svg{
            transform: scale(6);
        }
    }
}
.bg{
    position: fixed;
    top: 0;
    right: 0;
    transition: 1s ease-in-out;
    background-color: white;
    border-bottom: 100vh solid black;
	border-left: 10vw solid transparent;
	height: 0;
	width: 50%;
    &.full{
        border-left: 0 solid transparent;
        width: 100%;
    }
    @include fp{
        display: none;
    
    }
}
.bg-white{
    position: fixed;
    top: 0;
    left: 0;
    transition: 1s ease-in-out;
    background-color: white;
	height: 100vh;
	width: 45%;
    &.disapear{
        width: 0;
    }
    &.full{
        width: 100%;
    }
    @include fp{
        display: none;
    
    }
}