.swiper{
    position: unset;
    &-button-prev,
    &-button-next{
        width: 50px;
        height: 50px;
        top: unset !important;
        bottom: -70px;
        background-color: white;
        border-radius: 50%;
        border: solid 1px map-get($color, sub1);
        transition: .3s ease;
        @include rp(pl){
            width: 35px;
            height: 35px;
            bottom: -40px;
        }
        &::after{
            content: "";
            border-top: 10px solid transparent;
            border-bottom: 10px solid transparent;
            width: 0; 
            height: 0; 
            position: absolute;
            @include rp(pl){
                border-top: 5px solid transparent;
                border-bottom: 5px solid transparent;
            }
        }
        
    }
    &-button-prev{
        left: unset !important;
        right: 100px;
        @include rp(pl){
            right: 70px;
        }
        &:hover{
            right: 110px;
            @include rp(pl){
                right: 80px;
            }
        }
        &::after{
            right: 20px;
            border-right:15px solid map-get($color, sub1); 
            @include rp(pl){
                right: 15px;
                border-right:10px solid map-get($color, sub1); 
            }
            
        }
    }
    &-button-next{
        right: 10px;
        &:hover{
            right: 0;
        }
        &::after{
            left: 20px;
            border-left: 15px solid map-get($color, sub1);
            @include rp(pl){
                left: 15px;
                border-left: 10px solid map-get($color, sub1);
            }
        }
    }
}
