@import '../Base/all';
.edu-cer_right-items{
    margin-bottom: 20px;
    .right-items_name{
        @include title(s40);
        color: map-get($color, text2 );
        position: relative;
        // padding-left: 40px;
        margin-bottom: 5px;
        &::after{
            position: absolute;
            left: 15px;
            top: 50%;
            transform: translateY(-50%);
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color:  map-get($color, text2 );
            content: "";
        }
    }
    .right-items_decs{
        margin-bottom: 5px;
        color: map-get($color, text3 );
        @include title(s24);
    }
    &>*{
        padding-left: 40px;
    }
}