.title-header{
    &>*{
        @include title(s84);
    }
}
.title-color-bottom{
    display: inline-block;
    p{
        position: relative;
        display: inline-block;
        z-index: 2;
        &::after{
            transition: .3s ease-in-out;
            position: absolute;
            bottom: 0px;
            right: -35px;
            width: 150px;
            content: "";
            height: 50px;
            background: linear-gradient(90deg, rgba(255,255,255,1) 32%, rgba(255,193,7,1) 100%);
            z-index: -1;
            border-radius: 20px;
            border-radius: 20px;
            @include rp(mx){
                width: 80px;
                height: 30px;
            }
        }
    }
    
}