@import '../../style/Base/all';


.loading-page{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 1s ease;
    overflow: hidden;
    
    svg{
        width: 15vw;
            padding: 10px;
            width: 100%;
            transition:transform .3s ease-out;
            path{
                transition: .3s ease-in-out;
            }
            circle{
                transition: .3s ease-in-out;
            }
            #Logo_1{
                fill: transparent;
                stroke: 10;
                stroke: #3a3a3a;
                stroke-dasharray: 250;
                stroke-dashoffset: 250;
                animation: animate_1 infinite 2.3s linear forwards;
                
            }
            #Logo_2{
                // fill: #3a3a3a;
                stroke: 10;
                // stroke: #3a3a3a;
                path{
                    fill: transparent;
                    stroke: 10;
                    stroke: #3a3a3a;
                    stroke-dasharray: 250;
                    stroke-dashoffset: 250;
                    animation: scale_2 infinite 2.3s ease-in-out forwards ;
                    animation-delay: .3s;
                }

            }
    }
    
    &.loaded{
        top: -100%;
        svg{
            
            #Logo_1{
                animation: animate_1 1 1.3s linear forwards;
                
            }
            #Logo_2{
                path{
                    animation: scale_2 1 1.3s ease-in-out forwards ;
                }
                

            }
        }
    }


@keyframes animate_1 {
    0%{
        stroke-dashoffset: 0;
    }
    40%{
        stroke-dashoffset: 250;
    }
    70%{
        stroke-dashoffset: 500;
        fill: transparent;
    }
    100%{
        stroke-dashoffset: 500;
        fill: #3a3a3a;
    }


}

@keyframes scale_2 {
    0%{
        stroke-dashoffset: 0;
    }
    40%{
        stroke-dashoffset: 250;
    }
    70%{
        stroke-dashoffset: 500;
        fill: transparent;
    }
    100%{
        stroke-dashoffset: 500;
        fill: #3a3a3a;
    }


}
}