@import '../Base/all';
.block-skill{
    width: 100%;
    &_items{
        height: 100%;

    }
    &_wrapper{
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &_name{
        width: 70%;
        &>*{
            @include title(s32);
            text-align: center;
        }

    }
    &_percent{
        margin-top: auto;
        width: 100%;
        cursor: pointer;
        .skill-percent {
            width: 65%;
            margin: auto;
            justify-content: space-around ;
            @include rp(mm){
                width: 80%;
            }
            transition: .2s cubic-bezier(0.075, 0.82, 0.165, 1);
            &.active {
                transform: scale(1.1);
                .circle-round{
                    stroke-width: 3.8;
    
                }
                .circle-bg{
                    stroke-width: 2.8;
                }
            }
        }
        
          
          
        .circle{
            &-chart{
                display: block;
                margin: 10px auto;
                max-width: 80%;
                max-height: 250px;
                &.blue .circle-round{
                    stroke: map-get($color, sub1 );
                }
            }
            &-bg {
                fill: none;
                stroke: #eee;
                stroke-width: 3.8;
                transition: .3s;
            }
            &-round{
                transition: .3s;
                fill: none;
                stroke-width: 2.8;
                stroke-linecap: round;
                animation: progress 1s ease-out forwards;
            }
        }
        @keyframes progress {
            0% {
                stroke-dasharray: 0 100;
            }
        }
        
        
        .percentage {
            fill: map-get($color, text8 );
            // font-family: sans-serif;
            font-size: 0.3em;
            
            text-anchor: middle;
        }
    }
}
  