@import '../../Base/all';
.Home_5{
    height: 100%;
    position: relative;
    @include fp{
        @include section(s48, padding-bottom);
        @include section(s48, padding-top);
    }
    &-wrapper{
        width: 100%;
        height: 100%;
        
        &_infor{
            position: relative;
            height: 100%;
            width: 60%;
            margin-left: auto;
            z-index: 2;
            display: flex;
            align-items: center;
            @include fp{
                width: 100%;
            }
            .dp-exp{
                @include fp{
                    width: 100%;
                }
                &_title{
                    @include section(s70,margin-bottom);
                }
                &_details{
                    display: flex;
                    align-items: flex-start;
                    @include fp{
                        width: 100%;
                    }@include rp(ml){
                        flex-direction: column;
        
                    }
                }
            }
            
        }&_img{
            position: absolute;
            bottom: 0;
            left: 100px;
            transform: unset;
            width: 600px;
            @include rp(pm){
                left: 0px;
                width: 400px;
            }
            @include fp{
                display: none;
            }
            filter: blur(5px);
            -webkit-filter: blur(5px);
            @media (min-width:1200px){
                width: 42vw;
                max-width: 800px;
                // left: 50%;
                // transform: translateX(-50%);
            };
            z-index: 1;
        }
    }
}