@import '../../style/Base/all';
$circle : 60px;

.block-exp{
    &_items{
        max-width: 350px;
        @include rp(ml){
            max-width: 100%;
        }
        &:not(:last-child){

            @include section(s100,margin-right);
            @include rp(ml){
                @include section(s70,margin-right);
                @include section(s70,margin-bottom);
            }
        }
    }
    &_title{
        @include title(s30);
        margin-bottom: 10px;
        position: relative;
        &>*{
            position: relative;
            z-index: 2;

        }
        &::after{
            z-index: 1;
            position: absolute;
            top: calc(-#{$circle} / 2);
            left: calc(-#{$circle} / 2);
            content: "";
            height: $circle;
            width: $circle;
            border-radius: 50%;
            background-color: map-get($color, sub1 );
        }
    }
    &_time{
        @include section(s30,padding-left);
        position: relative;
        @include section(s30,margin-bottom);
        color: map-get($color, text3 );
        &::after{
            position: absolute;
            top: 8px;
            left: 0;
            content: "";
            height: 5px;
            width: 15px;
            border-radius: 10px;
            background-color: map-get($color, text3 );
        }
    }
    &_company{
        @include title(s28);
        @include section(s30,margin-bottom);
    }
    &_details{
        @include title(s20);
        color: map-get($color, text3 );
    }
}
