@import '../../Base/all';
#dp-information{
    width: 100%;
    height: 100%;
    // background-color: map-get($color, text2 );
    transition: .3s ease-in-out;
    overflow: hidden;
    position: relative;
    @include fp{
        background-color: map-get($color, bg );
        padding-bottom: 30px;
    }
    &::after{
        content: "";
        width: 1000px;
        height: 1000px;
        background-color: map-get($color, text1 );
        position: absolute;
        top: 50%;
        left: 10%;
        transform: rotate(120deg) translate(-70%, -30%)
    }
    .information{
        @include section(s70, padding-bottom);
        @include section(s70, padding-top);
        height: 100%;
        width: 100%;
        .container{
            height: 100%;
        }
        &__wrapper{
            display: flex;
            flex-direction: column;
            /* align-items: center; */
            justify-content: center;
            width: 100%;
            &--title{
                color: map-get($color, text2 );
            }
            &--list{
                position: relative;
                @include section(s70, padding-top);
                display: flex;
                flex-wrap: wrap;
                margin: 0 -8px;
            }
        }
    }
}