
@import "../Base/all";
.block_edu-cer{
    width: 100%;
    height: 100%;
    // padding-bottom: 30%;
    // position: relative;
    
    &_items{
        height: 100%;
        display: flex;
        // align-items: center;
        // justify-content: center;
        // flex: 1 1;
    }
    &_left{
        padding: 10px 5px;
        background-color: map-get($color, text3 );
        width: 20%;
        flex: 0 20%;
        display: flex;
        align-items: center;
        justify-content: center;
        border-top-left-radius: 20px;
        border-bottom-left-radius: 20px;
        box-shadow: map-get($shadow, s1);
    }
    &_right{
        width: 80%;
        flex: 0 80%;
        padding: 30px 20px;
        height: 100%;
        background-color: map-get($color, bg-4 );
        border-top-right-radius: 20px;
        border-bottom-right-radius: 20px;
        box-shadow: map-get($shadow, s1);
        @include rp(pl){
            padding: 20px;
        }
        @include rp(mx){
            padding: 10px;
        }
    }
    &_name{
        @include title(s24);
        margin-bottom: 20px;
    }
}