	primary: 'Kdam+Thmor+Pro',
);

$KdamThmorPro: 'Kdam Thmor Pro', sans-serif;


$color: (
	main: #3A3A3A,
	
	bg: #3A3A3A,
	bg-2: #D7E3F8,
	bg-3: #B7C8E6,
	bg-4: #F1F1F1,

	sub1: #FFC107,
	sub2: #D8FCE7,
	bg1: #EFEFEF,

	text1: #3A3A3A,
	text2: #F1F1F1,
	text3: #707070,
	text4: #FFF,
	text5: #606060,
	text6: #292929,
