	),
	s70: (
		default: 70px,
		pl: 55px,
		tx: 45px,
		mx: 40px
	),
	s48:(
		default: 48px,
		tx: 44px,
		mx: 36px,
	),
	s30:(
		default: 30px,
		tx: 26px,
		mx: 20px,
	),
	s150:(
		default: 150px,
		tx: 100px,
		mx: 50px,
	)
);

$fpsize:(
	w: 1024px,
	h: 540px
);
